.feedback-form {
   display: flex;
   flex-direction: column;
   max-width: 480px;
   padding: 12px;
  //  background-color: #fff;
   background-color: turquoise;
   border: 1px solid rgba(0, 0, 0, 0.12);
   border-radius: 4px;
 }
 
 .feedback-form label {
   display: flex;
   flex-direction: column;
   margin-bottom: 16px;
 }
 
 .feedback-form button {
   padding: 8px 12px;
   font: inherit;
   cursor: pointer;
 }
 
 input,
 textarea {
   padding: 8px;
   font: inherit;
 }
 
 textarea {
   resize: none;
 }
 